import { CreateParams, CreateResult, DataProvider, GetListResult, GetOneParams, GetOneResult, Identifier, UpdateParams, UpdateResult, GetManyParams, GetManyResult } from "react-admin"
import { EVENTS_URL } from "../api-urls"
import { CacheableDataProviderExtensionResult, DataProviderExtensionResult } from "../common/data-provider"
import { DateTime } from "../common/date-time"
import { get, getWithPagination, patch, post, put } from "../common/fetch.utils"
import { filterParamsComposer, queryParamsComposer } from "../common/get-by-conditions.utils"
import { TicketAPIGetListParams } from "../common/ticket-api-get-list.params"
import { EventCompaniesDto } from "../dto/event/event-companies.dto"
import { EventDevicesDto } from "../dto/event/event-devices.dto"
import { EventObjectsDto } from "../dto/event/event-objects.dto"
import { EventDto } from "../dto/event/event.dto"
import { EventStatus } from "../enum/EventStatus"
import filterMapper from "./event-filter.mapper"
import { mapSortEventParam } from './event-sort.mapper'

const provider = {
  getList: async (
    resource: string,
    {
      filter: { ...filter },
      sort,
      pagination
    }: TicketAPIGetListParams,
  ): Promise<GetListResult<EventDto>> => {
    const filterParams = `o=>${filterParamsComposer('o', filter, filterMapper)}`
    const pathParams = queryParamsComposer(sort, pagination, mapSortEventParam)
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const { data, range: { total } } = await getWithPagination<EventDto[]>(`${EVENTS_URL}/GetByConditions`, path)

    return Promise.resolve({
      data,
      total
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<EventDto>> => {
    const event = await get<EventDto>(EVENTS_URL, `/${id}`)

    return {
      data: event
    }
  },
  create: async (
    resource: string,
    { data }: CreateParams
  ): Promise<CreateResult<EventDto>> => {
    const event = await post<CreateEventRequest[], EventDto[]>(EVENTS_URL, [{
      ...data,
      dateStart:
        new Date(new Date(data?.dateStart).getTime() - (new Date(data?.dateStart).getTimezoneOffset() * 60000)).toJSON() as DateTime,
      dateEnd:
        new Date(new Date(data?.dateEnd).getTime() - (new Date(data?.dateEnd).getTimezoneOffset() * 60000)).toJSON() as DateTime
    }])

    return {
      data: event[0]
    }
  },
  update: async (
    resource: string,
    { id, data, previousData }: UpdateParams<EventDto>
  ): Promise<UpdateResult> => {
    const update = await patch<{ id: Identifier, [n: string]: any }, EventDto>(EVENTS_URL, {
      id,
      name: data.name !== previousData.name ? data.name : undefined,
      dateStart: data.dateStart !== previousData.dateStart ? new Date(new Date(data.dateStart).getTime() - (new Date(data.dateStart).getTimezoneOffset() * 60000)).toJSON() as DateTime : undefined,
      dateEnd: data.dateEnd !== previousData.dateEnd ? new Date(new Date(data.dateEnd).getTime() - (new Date(data.dateEnd).getTimezoneOffset() * 60000)).toJSON() as DateTime : undefined
    })
    return Promise.resolve({ data: update })
  },
  getMany: async (
    resource: string,
    { ids }: GetManyParams
  ): Promise<GetManyResult<EventDto>> => {
    const data = await get<EventDto[]>(EVENTS_URL,
      `/GetByConditions/e=>new int[] {${ids.toString()}}.Contains(e.Id)`)
    return Promise.resolve({ data })
  },
  activate: async (
    resource: string,
    { eventId }: ChangeStateParams
  ): Promise<DataProviderExtensionResult<ChangeStateResponse>> => {
    const activate = await put<undefined, ChangeStateResponse>
      (`${EVENTS_URL}/ActivateAsyncJob/${eventId}`, undefined)
    return {
      data: activate
    }
  },
  deactivate: async (
    resource: string,
    { eventId }: ChangeStateParams
  ): Promise<DataProviderExtensionResult<ChangeStateResponse>> => {
    const deactivate = await put<undefined, ChangeStateResponse>
      (`${EVENTS_URL}/DeactivateAsyncJob/${eventId}`, undefined)
    return {
      data: deactivate
    }
  },
  getCompaniesList: async (
    resource: string,
    { eventId }: GetCompaniesParams
  ): Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>> => {
    const companies = await get<EventCompaniesDto>
      (`${EVENTS_URL}`, `/GetCompanies/${eventId}`)
    return {
      data: companies
    }
  },
  attachCompanies: async (
    resource: string,
    params: EventCompaniesDto
  ): Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>> => {
    const eventCompanies = await put<EventCompaniesDto, EventCompaniesDto>
      (`${EVENTS_URL}/AttachCompanies`, params)
    return {
      data: eventCompanies
    }
  },
  detachCompanies: async (
    resource: string,
    params: EventCompaniesDto
  ): Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>> => {
    const eventCompanies = await put<EventCompaniesDto, EventCompaniesDto>
      (`${EVENTS_URL}/DetachCompanies`, params)
    return {
      data: eventCompanies
    }
  },
  attachObjects: async (
    resource: string,
    params: EventObjectsDto
  ): Promise<CacheableDataProviderExtensionResult<EventObjectsDto>> => {
    const eventObjects = await put<EventObjectsDto, EventObjectsDto>
      (`${EVENTS_URL}/AttachObjects`, params)
    return {
      data: eventObjects
    }
  },
  detachObjects: async (
    resource: string,
    params: EventObjectsDto
  ): Promise<CacheableDataProviderExtensionResult<EventObjectsDto>> => {
    const eventObjects = await put<EventObjectsDto, EventObjectsDto>
      (`${EVENTS_URL}/DetachObjects`, params)
    return {
      data: eventObjects
    }
  },
  getDevicesList: async (
    resource: string,
    { eventId }: GetDevicesParams
  ): Promise<CacheableDataProviderExtensionResult<EventDevicesDto>> => {
    const devices = await get<EventDevicesDto>
      (`${EVENTS_URL}`, `/GetDevices/${eventId}`)
    return {
      data: devices
    }
  },
  attachDevices: async (
    resource: string,
    params: EventDevicesDto
  ): Promise<CacheableDataProviderExtensionResult<EventDevicesDto>> => {
    const eventDevices = await put<EventDevicesDto, EventDevicesDto>
      (`${EVENTS_URL}/AttachDevices`, params)
    return {
      data: eventDevices
    }
  },
  detachDevices: async (
    resource: string,
    params: EventDevicesDto
  ): Promise<CacheableDataProviderExtensionResult<EventDevicesDto>> => {
    const eventDevices = await put<EventDevicesDto, EventDevicesDto>
      (`${EVENTS_URL}/DetachDevices`, params)
    return {
      data: eventDevices
    }
  }
} as EventDataProvider

interface EventDataProvider extends DataProvider {
  activate: (
    resource: string,
    params: ChangeStateParams
  ) => Promise<DataProviderExtensionResult<ChangeStateResponse>>;

  deactivate: (
    resource: string,
    params: ChangeStateParams
  ) => Promise<DataProviderExtensionResult<ChangeStateResponse>>;

  getCompaniesList: (
    resource: string,
    params: GetCompaniesParams
  ) => Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>>;

  attachCompanies: (
    resource: string,
    params: EventCompaniesDto
  ) => Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>>;

  detachCompanies: (
    resource: string,
    params: EventCompaniesDto
  ) => Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>>;

  getObjectsList: (
    resource: string,
    params: GetObjectsParams
  ) => Promise<CacheableDataProviderExtensionResult<EventObjectsDto>>;

  attachObjects: (
    resource: string,
    params: EventObjectsDto
  ) => Promise<CacheableDataProviderExtensionResult<EventObjectsDto>>;

  detachObjects: (
    resource: string,
    params: EventObjectsDto
  ) => Promise<CacheableDataProviderExtensionResult<EventObjectsDto>>;

  getDevicesList: (
    resource: string,
    params: GetDevicesParams
  ) => Promise<CacheableDataProviderExtensionResult<EventDevicesDto>>;

  attachDevices: (
    resource: string,
    params: EventDevicesDto
  ) => Promise<CacheableDataProviderExtensionResult<EventDevicesDto>>;

  detachDevices: (
    resource: string,
    params: EventDevicesDto
  ) => Promise<CacheableDataProviderExtensionResult<EventDevicesDto>>;
};

export interface ChangeStateParams {
  eventId: Identifier;
}

export interface ChangeStateResponse {
  jobId: Identifier;
}

export interface GetCompaniesParams {
  eventId: Identifier;
}

export interface GetObjectsParams {
  eventId: Identifier;
}

export interface GetDevicesParams {
  eventId: Identifier;
}

export interface CreateEventRequest {
  readonly name: string
  readonly status?: EventStatus
  readonly dateStart: DateTime
  readonly dateEnd: DateTime
  readonly externalId?: string
  readonly infix?: string
  readonly objectsIds?: number[]
}

export default provider