import { Box, Divider, Grid, Typography, useMediaQuery, useTheme } from '@material-ui/core'
import * as React from 'react'
import {
  Datagrid,
  List,
  ListProps,
  TextField,
  useListContext,
  useTranslate,
  FunctionField,
  Record,
  Pagination,
  TextInput,
  Filter
} from 'react-admin'
import { maskMiddlePart } from '../../core/common/mask.utils'
import { ResourceName } from '../../core/ResourceName'
import { TicketInfo } from '../../core/ticket-info/ticket-info'
import { MaskField } from '../common/MaskField'
import FilterProps from '../common/FilterProps'

const TicketInfoExpand = ({ record }: { record?: TicketInfo }) => {
  const t = useTranslate()
  return (
    <Box padding={1}>
      <Grid container spacing={1}>
        <Grid item xs={4}>
          {t("resources.ticket-info.fields.bookingId")}
        </Grid>
        <Grid item xs={8} style={{ textAlign: "center" }}>
          <TextField record={record} source="bookingId" />
        </Grid>
      </Grid>
    </Box>
  )
}

const TicketInfoGrid = () => {
  const {
    loading,
    filterValues: { ticketCode },
    data
  } = useListContext()
  const t = useTranslate()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  return (
    <>
      {ticketCode &&
        <Box padding={1} display="inline-flex">
          <Typography variant="h5" paragraph style={{ wordBreak: "break-word" }} >
            {t('resources.ticket-info.search-list-title', { ticketCode })}
          </Typography>
        </Box>
      }
      <Divider />
      <Datagrid
        padding={smallScreen ? "checkbox" : "default"}
        loaded={!loading}
        rowClick={(id) => `${ResourceName.TICKET_DETAILS}/${data[id]?.bookingId}/show`}
        isRowExpandable={() => xSmallScreen}
        expand={<TicketInfoExpand />}
      >
        {!smallScreen && <TextField source="firstname" sortable={false} />}
        {!smallScreen && <TextField source="lastname" sortable={false} />}
        {!smallScreen && <MaskField source="pesel" sortable={false} />}
        {smallScreen && <FunctionField source="customer" sortable={false}
          render={(record: Record | undefined) =>
            `${record?.firstname} ${record?.lastname} ${record?.pesel ? maskMiddlePart(record.pesel, 2) : ""}`
          } />}
        <TextField source="ticketCode" sortable />
        <FunctionField
          label='resources.ticket-info.fields.eventName'
          render={(record: Record | undefined) => record?.eventId ? `${record?.eventName} ${record?.eventDate?.replace("T", " ")} ${record?.eventId}` : "-" } />
        {!xSmallScreen && <TextField source="bookingId" sortable />}
      </Datagrid>
    </>
  )
}

const TicketInfoFilters = (props: FilterProps) => (
  <Filter {...props}>
    <TextInput source="ticketCode" alwaysOn />
  </Filter>
)

export const TicketInfoList = (props: ListProps) => (
  <List exporter={false} perPage={20} sort={{ field: "id", order: "DESC" }}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />} {...props}
    filters={<TicketInfoFilters />}>
    <TicketInfoGrid />
  </List>
)
