import { DataProvider, GetOneParams, GetOneResult, GetManyParams, GetManyResult } from "react-admin"
import { ADMINISTRATOR_URL } from "../api-urls"
import { get } from "../common/fetch.utils"
import { UserDto } from "../dto/user.dto"

const provider = {
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<UserDto>> => {
    const data = await get<UserDto>(`${ADMINISTRATOR_URL}/GetUserById`, `/${id}`)
    return Promise.resolve({
      data
    })
  },
  getMany: async (
    resource: string,
    {
      ids
    }: GetManyParams,
  ): Promise<GetManyResult<UserDto>> => {
    const data = await get<UserDto[]>(`${ADMINISTRATOR_URL}/GetUsersByConditions`, `/u=>new int[] {${ids.toString()}}.Contains(u.Id)`)
    return Promise.resolve({
      data
    })
  }
} as DataProvider

export default provider