import {
  List,
  ListProps,
  TextField,
  DateField,
  Datagrid,
  FunctionField,
  useTranslate
} from 'react-admin'
import React from 'react'
import { Grid, useMediaQuery, useTheme } from '@material-ui/core'
import { AccessDateDto } from '../../../core/dto/access-date.dto'

const AccessDateExpand = ({ record }: { record?: AccessDateDto }) => {
  const translate = useTranslate()
  const theme = useTheme()
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  return (
    <Grid container spacing={1}>
      <Grid item xs={6}>
        {translate("resources.access-dates.fields.timeFrom")}
      </Grid>
      <Grid item xs={6}
        style={{ textAlign: "center" }}>
        <FunctionField<AccessDateDto>
          source="timeFrom"
          render={(r?: AccessDateDto) => (
            r?.timeFrom.substring(11, 19)
          )} />
      </Grid>
      <Grid item xs={6}>
        {translate("resources.access-dates.fields.timeTo")}
      </Grid>
      {record?.dayOfWeek && xSmallScreen &&
        <>
          <Grid item xs={6}
            style={{ textAlign: "center" }}>
            <FunctionField<AccessDateDto>
              source="timeTo"
              render={(r?: AccessDateDto) => (
                r?.timeTo.substring(11, 19)
              )} />
          </Grid>
          <Grid item xs={6}>
            {translate("resources.access-dates.fields.dayOfWeek")}
          </Grid>
          <Grid item xs={6}
            style={{ textAlign: "center" }}>
            <TextField source="dayOfWeek" />
          </Grid>
        </>}
    </Grid>
  )
}

export const AccessDatesList = (props: ListProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  const translate = useTranslate()
  return (
    <List
      {...props}
      exporter={false}
      perPage={20}
      sort={{ field: 'id', order: 'DESC' }}
    >
      <>
        <Datagrid
          isRowExpandable={() => smallScreen}
          expand={<AccessDateExpand />}
          padding={xSmallScreen ? 'checkbox' : 'default'} >
          <DateField source="activeFrom" />
          <DateField source="activeTo" />
          {!smallScreen && <FunctionField<AccessDateDto>
            source="timeFrom"
            render={(r?: AccessDateDto) => (
              r?.timeFrom.substring(11, 19)
            )} />}
          {!smallScreen && <FunctionField<AccessDateDto>
            source="timeTo"
            render={(r?: AccessDateDto) => (
              r?.timeTo.substring(11, 19)
            )} />}
          <FunctionField<AccessDateDto>
            source="type"
            render={(r?: AccessDateDto) => (
              translate(`resources.enums.accessDateTypes.${r?.type}`)
            )} />
          <TextField source="priority" />
          {!xSmallScreen && <TextField source="dayOfWeek" />}
        </Datagrid>
      </>
    </List>
  )
}
