import { Box, ButtonGroup, Divider, useMediaQuery, useTheme } from "@material-ui/core"
import React from "react"
import {
  ShowProps, Show, TabbedShowLayout, Tab, TextField, FunctionField, useTranslate, GET_LIST,
  useShowContext, TabbedShowLayoutProps, useMutation, useNotify, sanitizeFetchType, TextInput, BooleanInput,
  useListContext, TopToolbar, ResourceContextProvider, List, Pagination, Datagrid, TitleProps, EditButton
} from 'react-admin'
import { AccessDto } from "../../../core/dto/access.dto"
import { VariantDto } from "../../../core/dto/variant.dto"
import { ResourceName } from "../../../core/ResourceName"
import Button from '../../common/customized-mui-components/Button'
import { CheckboxField } from "../../common/CheckboxField"
import { useHasAuthority } from "../../hooks/useHasAuthority"
import { Authority } from "../../../core/auth/Authority"

type VariantTabsProps = Omit<TabbedShowLayoutProps, 'children'>

interface VariantTitleProps extends TitleProps {
  record?: VariantDto
}

const ShowTitle = (data: VariantTitleProps) => {
  const { record: variant } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.variants.titles.show')}: {variant?.id}. {variant?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: variant } = useShowContext<VariantDto>()
  const hasAuthority = useHasAuthority()
  return (
    <>
      <Divider />
      <Box p={1} display="block" textAlign="right">
        <EditButton
        basePath="/variants"
        record={variant}
        variant="contained"
        style={{ margin: '2px' }}
        disabled={!hasAuthority(Authority.EDIT_VARIANTS)} />
      </Box>
    </>
  )
}

const RelationTab = ({ source, mode }: { source: ResourceName.ACCESSES, mode: 'show' | 'edit' }) => {
  const [mutate] = useMutation()
  const notify = useNotify()
  const { record: variant, basePath, refetch } = useShowContext<VariantDto>()
  const translate = useTranslate()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))

  const handleAttach = async (idsToAttach: number[]) => {
    switch (source) {
      case ResourceName.ACCESSES:
        await mutate(
          {
            type: sanitizeFetchType('attachAccesses'),
            resource: ResourceName.VARIANTS,
            payload: {
              variantId: variant?.id,
              accessesIds: idsToAttach,
            },
          },
          { returnPromise: true },
        )
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    if (refetch) {
      refetch()
    }
  }

  const handleDetach = async (idsToDetach: number[]) => {
    switch (source) {
      case ResourceName.ACCESSES:
        await mutate(
          {
            type: sanitizeFetchType('detachAccesses'),
            resource: ResourceName.VARIANTS,
            payload: {
              variantId: variant?.id,
              accessesIds: idsToDetach,
            },
          },
          { returnPromise: true },
        )
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    if (refetch) {
      refetch()
    }
  }

  const isAttached = (relatedRecordId: number) => {
    switch (source) {
      case ResourceName.ACCESSES:
        return variant?.accessesIds?.includes(relatedRecordId)
      default:
        notify(`Source '${source}' is not implemented yet.`)
        return false
    }
  }

  const listFilters = () => {
    switch (source) {
      case ResourceName.ACCESSES:
        return ([<TextInput source="name" alwaysOn />,
        <BooleanInput label="common.related-records.actions.attached-only" source="filterVariantId" alwaysOn />])
      default: return ([])
    }
  }

  const listSort = () => {
    switch (source) {
      case ResourceName.ACCESSES:
        return ({ field: 'id', order: 'DESC' })
      default: return (undefined)
    }
  }

  const listColumns = () => {
    switch (source) {
      case ResourceName.ACCESSES:
        return ([
          <TextField source="id" label="ID" key='id' />,
          <TextField source="name" key='name' />,
          !smallScreen && <FunctionField<AccessDto>
            source="bonus"
            key="bonus"
            render={(r?: AccessDto) =>
              translate(`resources.enums.bonus.${r?.bonus}`)} />,
          !smallScreen && <TextField source="entranceId" key='entranceId' />])
      default: return ([])
    }
  }

  const Actions = () => {
    const { filterValues, refetch: refetchList } = useListContext<AccessDto>()
    const getFilteredRelatedRecordsIds = async () =>
      (await mutate(
        {
          type: sanitizeFetchType(GET_LIST),
          resource: source,
          payload: {
            filter: {
              ...filterValues
            }
          }
        },
        { returnPromise: true }
      ))?.data?.map((record: AccessDto) => record?.id)

    const handleAttachAll = async () => {
      await handleAttach(await getFilteredRelatedRecordsIds())
      if (filterValues?.filterVariantId === true) {
        refetchList()
      }
    }

    const handleDetachAll = async () => {
      await handleDetach(await getFilteredRelatedRecordsIds())
      if (filterValues?.filterVariantId === true) {
        refetchList()
      }
    }

    return (
      <TopToolbar>
        <ButtonGroup>
          <Button
            variant="contained"
            onClick={() => handleAttachAll()}
            label="common.related-records.actions.attach-all"
            useSmallVersionBreakpoint={false}
          />
          <Button
            variant="outlined"
            onClick={() => handleDetachAll()}
            label="common.related-records.actions.detach-all"
            useSmallVersionBreakpoint={false}
          />
        </ButtonGroup>
      </TopToolbar>
    )
  }

  const AttachedCheckbox = ({ ...props }) => {
    const { filterValues, refetch: refetchList } = useListContext<AccessDto>()

    return (
      <CheckboxField
        {...props}
        disabled={mode !== 'edit'}
        sortable={false}
        checkBySource={(id: number) => isAttached(id)}
        source="id"
        onClick={
          (id: number) => isAttached(id as number)
            ? async () => {
              await handleDetach([id as number])
              if (filterValues?.filterVariantId === true) {
                refetchList()
              }
            }
            : async () => {
              await handleAttach([id as number])
              if (filterValues?.filterVariantId === true) {
                refetchList()
              }
            }
        } />
    )
  }

  return (
    <ResourceContextProvider value={source} >
      <List
        basePath={basePath}
        exporter={false}
        title=" "
        filters={listFilters()}
        filterDefaultValues={{ variantId: variant?.id, filterVariantId: true }}
        sort={listSort()}
        pagination={<Pagination rowsPerPageOptions={[10, 20, 50, 100]} />}
        perPage={20}
        actions={mode === 'edit' ? <Actions /> : <></>}
      >
        <>
          <Datagrid size="small" style={{ tableLayout: 'fixed' }} >
            {listColumns()}
            <AttachedCheckbox label="common.related-records.fields.is-attached" />
          </Datagrid>
        </>
      </List>
    </ResourceContextProvider>
  )
}

const VariantTabs = (props: VariantTabsProps) => {
  const translate = useTranslate()
  const hasAuthority = useHasAuthority()
  return (
    <TabbedShowLayout {...props}>
      <Tab label="resources.variants.tabs.general">
        <TextField source="id" />
        <TextField source="name" />
        <FunctionField<VariantDto>
          source="active"
          render={(r?: VariantDto) =>
            <div>
              {r?.active ?
                translate('const.yes') :
                translate('const.no')}
            </div>} />
        <GeneralActions />
      </Tab>
      {hasAuthority(Authority.VIEW_ACCESSES) &&
        <Tab path="accesses"
          label="resources.variants.tabs.accesses">
          <RelationTab source={ResourceName.ACCESSES}
            mode={hasAuthority(Authority.EDIT_ACCESSES) ? 'edit' : 'show'} />
        </Tab>}
    </TabbedShowLayout>
  )
}

export const VariantShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <VariantTabs />
  </Show>
)