
const mapping: Record<
  string,
  (lambdaParameter: string, value: string | number | boolean, otherValues: any) => string | null
> = {
  id: (lambdaParameter, value) => value.toString().split(",").map((v) => `${lambdaParameter}.Id==${v}`).join("||"),
  name: (lambdaParameter, value) => `${(lambdaParameter)}.Name.Contains("${value}")`,
  bonus: (lambdaParameter, value) => `${(lambdaParameter)}.Bonus.Equals(TicketAPI.Models.Enums.TicketManagement.AccessBonuses.${value})`,
  entranceId: (lambdaParameter, value) => `${(lambdaParameter)}.EntranceId.Equals(${value})`,
  allowReenter: (lambdaParameter) => `${(lambdaParameter)}.AllowReenter`,
  multipleEntry: (lambdaParameter) => `${(lambdaParameter)}.MultipleEntry`,
  isTicketUnlockedWithNextDay: (lambdaParameter) => `${(lambdaParameter)}.IsTicketUnlockedWithNextDay`,
  filterVariantId: (lambdaParameter, value, otherValues) => value ? `${(lambdaParameter)}.Variants.Any(v=>v.Id.Equals(${otherValues?.variantId}))` : null,
  variantId: () => null,
}

export const filterMapper = (key: string) =>
  mapping[key] as (lambdaParameter: string, value: string | number | boolean, otherValues: any) => string

export default filterMapper