import { Box, ButtonGroup, Divider } from "@material-ui/core"
import React from "react"
import { BooleanInput, Datagrid, DateField, EditButton, FunctionField, GET_LIST, List, Pagination, ReferenceField, ResourceContextProvider, sanitizeFetchType, Show, ShowProps, Tab, TabbedShowLayout, TabbedShowLayoutProps, TextField, TextInput, TitleProps, TopToolbar, useListContext, useMutation, useNotify, useShowContext, useTranslate } from "react-admin"
import { Authority } from "../../../core/auth/Authority"
import { formatTime } from "../../../core/common/date-time"
import { AccessDto } from "../../../core/dto/access.dto"
import { EntranceDto } from "../../../core/dto/entrance.dto"
import { VariantDto } from "../../../core/dto/variant.dto"
import { AccessActivePeriodStartingOptions } from "../../../core/enum/AccessActivePeriodStartingOptions"
import { ResourceName } from "../../../core/ResourceName"
import { CheckboxField } from "../../common/CheckboxField"
import Button from '../../common/customized-mui-components/Button'
import { useHasAuthority } from "../../hooks/useHasAuthority"

type AccessTabsProps = Omit<TabbedShowLayoutProps, 'children'>

interface AccessTitleProps extends TitleProps {
  record?: AccessDto
}

const ShowTitle = (data: AccessTitleProps) => {
  const { record: variant } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.accesses.titles.show')}: {variant?.id}. {variant?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: access } = useShowContext<AccessDto>()
  const hasAuthority = useHasAuthority()
  return (
    <>
      <Divider />
      <Box p={1} display="block" textAlign="right">
        <EditButton
          basePath="/accesses"
          record={access}
          variant="contained"
          style={{ margin: '2px' }}
          disabled={!hasAuthority(Authority.EDIT_ACCESSES)} />
      </Box>
    </>
  )
}

const RelationTab = ({ source, mode }: { source: ResourceName.VARIANTS, mode: 'show' | 'edit' }) => {
  const [mutate] = useMutation()
  const notify = useNotify()
  const { record: access, basePath, refetch } = useShowContext<AccessDto>()
  const translate = useTranslate()

  const handleAttach = async (idsToAttach: number[]) => {
    switch (source) {
      case ResourceName.VARIANTS:
        await mutate(
          {
            type: sanitizeFetchType('attachVariants'),
            resource: ResourceName.ACCESSES,
            payload: {
              accessId: access?.id,
              variantsIds: idsToAttach,
            },
          },
          { returnPromise: true },
        )
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    if (refetch) {
      refetch()
    }
  }

  const handleDetach = async (idsToDetach: number[]) => {
    switch (source) {
      case ResourceName.VARIANTS:
        await mutate(
          {
            type: sanitizeFetchType('detachVariants'),
            resource: ResourceName.ACCESSES,
            payload: {
              accessId: access?.id,
              variantsIds: idsToDetach,
            },
          },
          { returnPromise: true },
        )
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    if (refetch) {
      refetch()
    }
  }

  const isAttached = (relatedRecordId: number) => {
    switch (source) {
      case ResourceName.VARIANTS:
        return access?.variantsIds?.includes(relatedRecordId)
      default:
        notify(`Source '${source}' is not implemented yet.`)
        return false
    }
  }

  const listFilters = () => {
    switch (source) {
      case ResourceName.VARIANTS:
        return ([<TextInput source="name" alwaysOn />,
        <BooleanInput label="common.related-records.actions.attached-only" source="filterAccessId" alwaysOn />])
      default: return ([])
    }
  }

  const listSort = () => {
    switch (source) {
      case ResourceName.VARIANTS:
        return ({ field: 'id', order: 'DESC' })
      default: return (undefined)
    }
  }

  const listColumns = () => {
    switch (source) {
      case ResourceName.VARIANTS:
        return ([
          <TextField source="id" label="ID" key='id' />,
          <TextField source="name" key='name' />,
          <FunctionField<VariantDto>
            source="active"
            key="active"
            render={(r?: VariantDto) =>
              r?.active ?
                translate('const.yes') :
                translate('const.no')} />])
      default: return ([])
    }
  }

  const Actions = () => {
    const { filterValues, refetch: refetchList } = useListContext<VariantDto>()
    const getFilteredRelatedRecordsIds = async () =>
      (await mutate(
        {
          type: sanitizeFetchType(GET_LIST),
          resource: source,
          payload: {
            filter: {
              ...filterValues
            }
          }
        },
        { returnPromise: true }
      ))?.data?.map((record: VariantDto) => record?.id)

    const handleAttachAll = async () => {
      await handleAttach(await getFilteredRelatedRecordsIds())
      if (filterValues?.filterAccessId === true) {
        refetchList()
      }
    }

    const handleDetachAll = async () => {
      await handleDetach(await getFilteredRelatedRecordsIds())
      if (filterValues?.filterAccessId === true) {
        refetchList()
      }
    }

    return (
      <TopToolbar>
        <ButtonGroup>
          <Button
            variant="contained"
            onClick={() => handleAttachAll()}
            label="common.related-records.actions.attach-all"
            useSmallVersionBreakpoint={false}
          />
          <Button
            variant="outlined"
            onClick={() => handleDetachAll()}
            label="common.related-records.actions.detach-all"
            useSmallVersionBreakpoint={false}
          />
        </ButtonGroup>
      </TopToolbar>
    )
  }

  const AttachedCheckbox = ({ ...props }) => {
    const { filterValues, refetch: refetchList } = useListContext<VariantDto>()

    return (
      <CheckboxField
        {...props}
        disabled={mode !== 'edit'}
        sortable={false}
        checkBySource={(id: number) => isAttached(id)}
        source="id"
        onClick={
          (id: number) => isAttached(id as number)
            ? async () => {
              await handleDetach([id as number])
              if (filterValues?.filterAccessId === true) {
                refetchList()
              }
            }
            : async () => {
              await handleAttach([id as number])
              if (filterValues?.filterAccessId === true) {
                refetchList()
              }
            }
        } />
    )
  }

  return (
    <ResourceContextProvider value={source} >
      <List
        basePath={basePath}
        exporter={false}
        title=" "
        filters={listFilters()}
        filterDefaultValues={{ accessId: access?.id, filterAccessId: true }}
        sort={listSort()}
        pagination={<Pagination rowsPerPageOptions={[10, 20, 50, 100]} />}
        perPage={20}
        actions={mode === 'edit' ? <Actions /> : <></>}
      >
        <>
          <Datagrid size="small" style={{ tableLayout: 'fixed' }} >
            {listColumns()}
            <AttachedCheckbox label="common.related-records.fields.is-attached" />
          </Datagrid>
        </>
      </List>
    </ResourceContextProvider>
  )
}

const AccessTabs = (props: AccessTabsProps) => {
  const { record: access } = props
  const translate = useTranslate()
  const hasAuthority = useHasAuthority()

  return (
    <TabbedShowLayout {...props}>
      <Tab
        label="resources.accesses.tabs.details">
        <TextField source="id" />
        <TextField source="name" />
        <FunctionField<AccessDto>
          source="bonus"
          render={(r?: AccessDto) =>
            <div>
              {translate(`resources.enums.bonus.${r?.bonus}`)}
            </div>
          }
        />
        <ReferenceField
          source="entranceId"
          reference={ResourceName.ENTRANCES}
          link={false}
        >
          <FunctionField<EntranceDto>
            render={(record?: EntranceDto) =>
              <div>{record?.id} ({record?.note})</div>} />
        </ReferenceField>
        <ReferenceField
          source="modifiedById"
          reference={ResourceName.USERS}
          link={false}
        >
          <TextField
            source="userName" />
        </ReferenceField>
        <ReferenceField
          source="createdById"
          reference={ResourceName.USERS}
          link={false}
        >
          <TextField
            source="userName" />
        </ReferenceField>
        <FunctionField<AccessDto>
          source="allowReenter"
          render={(r?: AccessDto) =>
            <div>
              {r?.allowReenter
                ? translate('const.yes')
                : translate('const.no')}
            </div>} />
        <FunctionField<AccessDto>
          source="multipleEntry"
          render={(r?: AccessDto) =>
            <div>
              {r?.multipleEntry
                ? translate('const.yes')
                : translate('const.no')}
            </div>} />
        <TextField source="multiple" />
        <TextField source="usePriority" />
        <TextField source="delay" />
        {access?.releaseTime &&
          <FunctionField<AccessDto>
            source="releaseTime"
            render={(record?: AccessDto) =>
              <div>{formatTime(record?.releaseTime)}</div>}
          />}
        {access?.releaseDayOfMonth &&
          <TextField source="releaseDayOfMonth" />}
        {access?.releaseDayOfWeek &&
          <FunctionField<AccessDto>
          source="releaseDayOfWeek"
          render={(r?: AccessDto) =>
            <div>
              {translate(
                `resources.enums.dayOfWeek.${r?.releaseDayOfWeek}`
              )}
            </div>} />}
        <DateField source="createdAt" showTime />
        <DateField source="modifiedAt" showTime />
        {access?.activePeriodStartingFrom !== AccessActivePeriodStartingOptions.NO_LIMIT &&
          <TextField source="activePeriod" />}
        <FunctionField<AccessDto>
          source="activePeriodStartingFrom"
          render={(r?: AccessDto) =>
            <div>
              {translate(
                `resources.enums.activePeriodStartingFrom.${r?.activePeriodStartingFrom}`
              )}
            </div>} />
        <GeneralActions />
      </Tab>
      {hasAuthority(Authority.VIEW_VARIANTS) &&
        <Tab path="variants"
          label="resources.accesses.tabs.variants">
          <RelationTab source={ResourceName.VARIANTS}
            mode={hasAuthority(Authority.EDIT_VARIANTS) ? 'edit' : 'show'} />
        </Tab>}
    </TabbedShowLayout>
  )
}

export const AccessShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <AccessTabs />
  </Show>
)