import { DataProvider, GetListResult, GetManyParams, GetManyReferenceResult, GetManyResult, GetOneParams, GetOneResult } from 'react-admin'
import { ENTRANCES_URL } from '../api-urls'
import { get, getWithPagination } from '../common/fetch.utils'
import { queryParamsComposer } from '../common/get-by-conditions.utils'
import { TicketAPIGetListParams, TicketAPIGetManyRefrenceParams } from '../common/ticket-api-get-list.params'
import { EntranceDto } from '../dto/entrance.dto'
import { getFilterMapper } from './entrance-filter.mapper'
import { mapSortEntranceParam } from './entrance-sort.mapper'

const getFilterParams = (
  partialValues: Partial<EntranceDto>
): string => {
  if (Object.keys(partialValues).length) {
    const filterParams = Object.entries(partialValues).map(([key, value]) => {
      const mapper = getFilterMapper(key)
      return mapper('o', value as string, partialValues)
    }).filter((val) => val != null)
    if (filterParams.length) {
      return filterParams.reduce((p, c) => `${p}&&${c}`)
    }
  }
  return 'true'
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { ...filter },
      sort,
      pagination
    }: TicketAPIGetListParams,
  ): Promise<GetListResult<EntranceDto>> => {
    const filterParams = `o=>${getFilterParams(filter)}`
    const pathParams = queryParamsComposer(sort, pagination, mapSortEntranceParam)
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const { data, range: { total } } = await getWithPagination<EntranceDto[]>(`${ENTRANCES_URL}/GetByConditions`, path)

    return Promise.resolve({
      data,
      total
    })
  },
  getMany: async (
    resource: string,
    {
      ids
    }: GetManyParams,
  ): Promise<GetManyResult<EntranceDto>> => {
    const { data } = await getWithPagination<EntranceDto[]>(`${ENTRANCES_URL}/GetByConditions/`, `e=>new int[] {${ids.toString()}}.Contains(e.Id)`)
    return Promise.resolve({
      data
    })
  },
  getManyReference: async (
    resource: string,
    {
      filter: { ...filter },
      sort,
      pagination
    }: TicketAPIGetManyRefrenceParams,
  ): Promise<GetManyReferenceResult<EntranceDto>> => {
    const filterParams = `o=>${getFilterParams(filter)}`
    const pathParams = queryParamsComposer(sort, pagination, mapSortEntranceParam)
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const { data, range: { total } } = await getWithPagination<EntranceDto[]>(`${ENTRANCES_URL}/GetByConditions`, path)

    return Promise.resolve({
      data,
      total
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<EntranceDto>> => {
    const data = await get<EntranceDto>(ENTRANCES_URL, `/${id}`)
    return Promise.resolve({
      data
    })
  }
} as DataProvider

export default provider