import { DataProvider, UpdateParams, UpdateResult, GetListResult } from 'react-admin'
import { getWithPagination, patch } from '../common/fetch.utils'
import { BOOKINGS_URL } from '../api-urls'
import { BookingUpdateRequest } from './booking-update.request'
import { BookingDto } from '../dto/booking.dto'
import filterMapper from './booking-filter.mapper'
import { mapSortBookingParam } from './booking-sort.mapper'
import { filterParamsComposer, queryParamsComposer } from '../common/get-by-conditions.utils'
import { TicketAPIGetListParams } from '../common/ticket-api-get-list.params'

const provider = {
  update: async (
    resource: string,
    { data }: UpdateParams<BookingUpdateRequest>,
  ): Promise<UpdateResult<BookingDto>> => {
    const booking = await patch<BookingUpdateRequest, BookingDto>(
      BOOKINGS_URL,
      data,
    )
    return Promise.resolve({
      data: booking,
    })
  },
  getList: async (
    resource: string,
    {
      filter,
      sort,
      pagination,
    }: TicketAPIGetListParams
  ): Promise<GetListResult<BookingDto>> => {
    const filterParams = `a=>${filterParamsComposer('a', filter, filterMapper)}`
    const pathParams = queryParamsComposer(sort, pagination, mapSortBookingParam)
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const {
      data,
      range: { total }
    } = await getWithPagination<BookingDto[]>(`${BOOKINGS_URL}/GetByConditions`, path)

    return {
      data,
      total
    }
  }
} as DataProvider

export default provider
