const useStyles = makeStyles(
    theme => ({
        // ...resettableStyles,
        input: {
            minWidth: theme.spacing(20),
        },
    }),
    { name: 'RaSelectInput' }
);


export interface SelectWithPaginationInputProps
    extends SelectInputProps {
    classes?: ClassesOverride<typeof useStyles>;
    paginationProps?: PaginationProps;
}

export const SelectWithPaginationInput = (props: SelectWithPaginationInputProps) => {
    const {
        allowEmpty,
        choices = [],
        // classes: classesOverride,
        className,
        create,
        createLabel,
        createValue,
        // disableValue,
        emptyText,
        emptyValue,
        format,
        helperText,
        label,
        // loaded,
        loading,
        margin = 'dense',
        onBlur,
        onChange,
        onCreate,
        onFocus,
        options,
        optionText,
        optionValue,
        parse,
        paginationProps,
        resource,
        source,
        translateChoice,
        validate,
        ...rest
    } = props;
    const translate = useTranslate();
    const classes = useStyles(props);

    warning(
        source === undefined,
        `If you're not wrapping the SelectInput inside a ReferenceInput, you must provide the source prop`
    );

    warning(
        choices === undefined,
        `If you're not wrapping the SelectInput inside a ReferenceInput, you must provide the choices prop`
    );

    const { getChoiceText, getChoiceValue } = useChoices({
        optionText,
        optionValue,
        translateChoice,
    });

    const { id, input, isRequired, meta } = useInput({
        format,
        onBlur,
        onChange,
        onFocus,
        parse,
        resource,
        source: source as string,
        validate,
        ...rest,
    });

    const { touched, error, submitError } = meta;

    const renderEmptyItemOption = useCallback(() =>
        React.isValidElement(emptyText) // eslint-disable-line no-nested-ternary
            ? React.cloneElement(emptyText)
            : emptyText === ''
                ? '' // em space, forces the display of an empty line of normal height
                : translate(emptyText, { _: emptyText }), [emptyText, translate]);

    const renderMenuItemOption = useCallback(choice => getChoiceText(choice), [
        getChoiceText,
    ]);

    const handleChange = useCallback(
        async (event: React.ChangeEvent<HTMLSelectElement>, newItem) => {
            if (newItem) {
                const value = getChoiceValue(newItem);
                input.onChange(value);
                return;
            }

            input.onChange(event);
        },
        [input, getChoiceValue]
    );

    const {
        getCreateItem,
        handleChange: handleChangeWithCreateSupport,
        createElement,
    } = useSupportCreateSuggestion({
        create,
        createLabel,
        createValue,
        handleChange,
        onCreate,
        optionText,
    });
    // if (loading) {
    //     return (
    //         <Labeled
    //             id={id}
    //             label={label}
    //             source={source}
    //             resource={resource}
    //             className={className}
    //             isRequired={isRequired}
    //             meta={meta}
    //             input={input}
    //             margin={margin}
    //         >
    //             <LinearProgress />
    //         </Labeled>
    //     );
    // }

    const renderCreateItem = () => {
        if (onCreate || create) {
            const createItem = getCreateItem();
            return (
                <MenuItem value={createItem.id} key={createItem.id}>
                    {createItem.name}
                </MenuItem>
            );
        }
        return null;
    };

    return (
        <>
            <ResettableTextField
                id={id}
                {...input}
                onChange={handleChangeWithCreateSupport}
                select
                label={
                    label !== '' &&
                    label !== false && (
                        <FieldTitle
                            label={label}
                            source={source}
                            resource={resource}
                            isRequired={isRequired}
                        />
                    )
                }
                className={`${classes.input} ${className}`}
                clearAlwaysVisible
                error={!!(touched && (error || submitError))}
                helperText={
                    <InputHelperText
                        touched={touched === true}
                        error={error || submitError}
                        helperText={helperText}
                    />
                }
                margin={margin}
                {...options}
            // {...sanitizeRestProps(rest)}
            >
                {allowEmpty ? (
                    <MenuItem
                        value={emptyValue}
                        key="null"
                        aria-label={translate('ra.action.clear_input_value')}
                        title={translate('ra.action.clear_input_value')}
                    >
                        {renderEmptyItemOption()}
                    </MenuItem>
                ) : null}
                {!loading && choices.map(choice => (
                    <MenuItem
                        key={getChoiceValue(choice)}
                        value={getChoiceValue(choice)}
                    // disabled={get(choice, disableValue)}
                    >
                        {renderMenuItemOption(choice)}
                    </MenuItem>
                ))}
                {renderCreateItem()}
                {!loading && <Pagination {...paginationProps} />}
            </ResettableTextField>
            {createElement}
        </>
    );
};

SelectWithPaginationInput.propTypes = {
    allowEmpty: PropTypes.bool,
    emptyText: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    emptyValue: PropTypes.any, // eslint-disable-line react/forbid-prop-types
    choices: PropTypes.arrayOf(PropTypes.object),
    classes: PropTypes.object, // eslint-disable-line react/forbid-prop-types
    className: PropTypes.string,
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    options: PropTypes.object, // eslint-disable-line react/forbid-prop-types
    optionText: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.func,
        PropTypes.element,
    ]).isRequired,
    optionValue: PropTypes.string.isRequired,
    disableValue: PropTypes.string,
    resettable: PropTypes.bool,
    resource: PropTypes.string,
    source: PropTypes.string,
    translateChoice: PropTypes.bool,
};

SelectWithPaginationInput.defaultProps = {
    emptyText: '',
    emptyValue: '',
    options: {},
    optionText: 'name', // eslint-disable-line react/default-props-match-prop-types
    optionValue: 'id', // eslint-disable-line react/default-props-match-prop-types
    translateChoice: true,
    disableValue: 'disabled',
};

