import { Box, ButtonGroup, Divider } from '@material-ui/core'
import React, { useMemo } from "react"
import { BooleanInput, Datagrid, DateField, EditButton, FilterButton, FunctionField, GET_LIST, List, Pagination, Record, ResourceContextProvider, sanitizeFetchType, Show, ShowProps, Tab, TabbedShowLayout, TextField, TextInput, TitleProps, TopToolbar, useListContext, useMutation, useNotify, useQuery, useRefresh, useShowContext, useTranslate } from "react-admin"
import { Authority } from '../../core/auth/Authority'
import { CompanyDto } from '../../core/dto/company.dto'
import { EngineDto } from "../../core/dto/device/engine/engine.dto"
import { PalmDto } from "../../core/dto/device/palm/palm.dto"
import { EventCompaniesDto } from '../../core/dto/event/event-companies.dto'
import { EventDevicesDto } from '../../core/dto/event/event-devices.dto'
import { EventDto } from '../../core/dto/event/event.dto'
import { ObjectDto } from '../../core/dto/object.dto'
import { EventStatus } from "../../core/enum/EventStatus"
import { ResourceName } from "../../core/ResourceName"
import { CheckboxField } from '../common/CheckboxField'
import Button from '../common/customized-mui-components/Button'
import { useHasAuthority } from '../hooks/useHasAuthority'

interface EventTitleProps extends TitleProps {
  record?: EventDto
}

const ShowTitle = (data: EventTitleProps) => {
  const { record: event } = data
  const translate = useTranslate()
  const date = useMemo(() => !((event?.dateStart as any) instanceof Date) ? event?.dateStart.replace('T', ' ') : '', [event?.dateStart])
  return (
    <span>{translate('resources.events.titles.show')}: {event?.name} {date} {event?.id}</span>
  )
}

const GeneralActions = () => {
  const { record: event } = useShowContext<EventDto>()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()
  const hasAuthority = useHasAuthority()

  const handleActivation = async () => {
    if (event?.status !== EventStatus.IN_TRANSITION) {
      if (event?.status === EventStatus.ACTIVE) {
        await mutate({
          type: sanitizeFetchType("deactivate"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id
          }
        }, { returnPromise: false })
      }
      else {
        await mutate({
          type: sanitizeFetchType("activate"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id
          }
        }, { returnPromise: false })
      }
      refresh()
    }
  }

  return (
    <>
      <Divider />
      <Box p={1} display="flex" justifyContent="flex-end">
        <Button variant="contained"
          onClick={handleActivation}
          color="primary"
          style={{ marginRight: '5px' }}
          label={event?.status === EventStatus.INACTIVE ?
            translate('resources.events.actions.activate') :
            translate('resources.events.actions.deactivate')}
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_EVENTS) || event?.status === EventStatus.IN_TRANSITION} />
        <EditButton variant="contained"
          basePath="/events"
          record={event}
          disabled={!hasAuthority(Authority.EDIT_EVENTS)} />
      </Box>
    </>
  )
}

const RelationTab = ({ source, mode }: { source: ResourceName.COMPANIES | ResourceName.OBJECTS | ResourceName.ENGINES | ResourceName.PALMS, mode: 'show' | 'edit' }) => {
  const [mutate] = useMutation()
  const notify = useNotify()
  const { record: event, basePath, refetch } = useShowContext<EventDto>()
  const getRelationsFetchType = () => {
    switch (source) {
      case ResourceName.COMPANIES:
        return `getCompaniesList`
      case ResourceName.ENGINES:
      case ResourceName.PALMS:
        return `getDevicesList`
      default:
        return ''
    }
  }
  const { data: relationsDto, refetch: refetchRelationsIds } = useQuery({
    type: sanitizeFetchType(getRelationsFetchType()),
    resource: ResourceName.EVENTS,
    payload: {
      eventId: event?.id,
    }
  }, {
    enabled: getRelationsFetchType() !== ''
  })

  const handleAttach = async (idsToAttach: number[]) => {
    switch (source) {
      case ResourceName.COMPANIES:
        await mutate({
          type: sanitizeFetchType("attachCompanies"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            companiesIds: idsToAttach
          }
        }, { returnPromise: true })
        break
      case ResourceName.OBJECTS:
        await mutate({
          type: sanitizeFetchType("attachObjects"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            objectsIds: idsToAttach
          }
        }, { returnPromise: true })
        break
      case ResourceName.ENGINES:
      case ResourceName.PALMS:
        await mutate({
          type: sanitizeFetchType("attachDevices"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            verifierDevicesIds: idsToAttach
          }
        }, { returnPromise: true })
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    if (refetch) {
      refetch()
    }
    refetchRelationsIds()
  }

  const handleDetach = async (idsToDetach: number[]) => {
    switch (source) {
      case ResourceName.COMPANIES:
        await mutate({
          type: sanitizeFetchType("detachCompanies"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            companiesIds: idsToDetach
          }
        }, { returnPromise: true })
        break
      case ResourceName.OBJECTS:
        await mutate({
          type: sanitizeFetchType("detachObjects"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            objectsIds: idsToDetach
          }
        }, { returnPromise: true })
        break
      case ResourceName.ENGINES:
      case ResourceName.PALMS:
        await mutate({
          type: sanitizeFetchType("detachDevices"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event && event.id,
            verifierDevicesIds: idsToDetach
          }
        }, { returnPromise: true })
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    if (refetch) {
      refetch()
    }
    refetchRelationsIds()
  }

  const isAttached = (relatedRecordId: number) => {
    switch (source) {
      case ResourceName.COMPANIES:
        return (relationsDto as EventCompaniesDto)?.companiesIds?.includes(relatedRecordId) === true
      case ResourceName.OBJECTS:
        return event?.objectsIds?.includes(relatedRecordId)
      case ResourceName.ENGINES:
      case ResourceName.PALMS:
        return (relationsDto as EventDevicesDto)?.verifierDevicesIds?.includes(relatedRecordId) === true
      default:
        notify(`Source '${source}' is not implemented yet.`)
        return false
    }
  }

  const listFilters = () => {
    switch (source) {
      case ResourceName.COMPANIES:
      case ResourceName.OBJECTS:
        return ([<TextInput source="name" alwaysOn />,
        <BooleanInput label="common.related-records.actions.attached-only" source="filterEventId" alwaysOn />])
      case ResourceName.ENGINES:
      case ResourceName.PALMS:
        return ([<TextInput source="name" alwaysOn />,
        <BooleanInput label="common.related-records.actions.attached-only" source="filterEventId" alwaysOn />,
        <TextInput source="categoryId" />])
      default: return ([])
    }
  }

  const listSort = () => {
    switch (source) {
      case ResourceName.COMPANIES:
      case ResourceName.OBJECTS:
      case ResourceName.ENGINES:
      case ResourceName.PALMS:
        return ({ field: 'name', order: 'ASC' })
      default: return (undefined)
    }
  }

  const listColumns = () => {
    switch (source) {
      case ResourceName.COMPANIES:
      case ResourceName.OBJECTS:
      case ResourceName.ENGINES:
      case ResourceName.PALMS:
        return ([
          <TextField source="id" label="ID" key='id' />,
          <TextField source="name" key='name' />])
      default: return ([])
    }
  }

  const Actions = () => {
    const { filterValues, refetch: refetchList } = useListContext<CompanyDto | ObjectDto | EngineDto | PalmDto>()
    const getFilteredRelatedRecordsIds = async () =>
      (await mutate(
        {
          type: sanitizeFetchType(GET_LIST),
          resource: source,
          payload: {
            filter: {
              ...filterValues
            }
          }
        },
        { returnPromise: true }
      ))?.data?.map((record: CompanyDto | ObjectDto | EngineDto | PalmDto) => record?.id)

    const handleAttachAll = async () => {
      await handleAttach(await getFilteredRelatedRecordsIds())
      if (filterValues?.filterEventId === true) {
        refetchList()
      }
    }

    const handleDetachAll = async () => {
      await handleDetach(await getFilteredRelatedRecordsIds())
      if (filterValues?.filterEventId === true) {
        refetchList()
      }
    }

    return (
      <TopToolbar>
        <FilterButton />
        <ButtonGroup>
          <Button
            variant="contained"
            onClick={() => handleAttachAll()}
            label="common.related-records.actions.attach-all"
            useSmallVersionBreakpoint={false}
          />
          <Button
            variant="outlined"
            onClick={() => handleDetachAll()}
            label="common.related-records.actions.detach-all"
            useSmallVersionBreakpoint={false}
          />
        </ButtonGroup>
      </TopToolbar>
    )
  }

  const AttachedCheckbox = ({ ...props }) => {
    const { filterValues, refetch: refetchList } = useListContext<CompanyDto | ObjectDto | EngineDto | PalmDto>()

    return (
      <CheckboxField
        {...props}
        disabled={mode !== 'edit'}
        sortable={false}
        checkBySource={(id: number) => isAttached(id)}
        source="id"
        onClick={
          (id: number) => isAttached(id as number)
            ? async () => {
              await handleDetach([id as number])
              if (filterValues?.filterEventId === true) {
                refetchList()
              }
            }
            : async () => {
              await handleAttach([id as number])
              if (filterValues?.filterEventId === true) {
                refetchList()
              }
            }
        } />
    )
  }

  return (
    <ResourceContextProvider value={source} >
      <List
        basePath={basePath}
        exporter={false}
        title=" "
        filters={listFilters()}
        filterDefaultValues={{ eventId: event?.id, filterEventId: true }}
        sort={listSort()}
        pagination={<Pagination rowsPerPageOptions={[10, 20, 50, 100]} />}
        perPage={20}
        actions={mode === 'edit' ? <Actions /> : <></>}
      >
        <>
          <Datagrid size="small" style={{ tableLayout: 'fixed' }} >
            {listColumns()}
            <AttachedCheckbox label="common.related-records.fields.is-attached" />
          </Datagrid>
        </>
      </List>
    </ResourceContextProvider>
  )
}

const EventTabs = ({ ...props }) => {
  const translate = useTranslate()

  return (
    <TabbedShowLayout {...props}>
      <Tab label={translate("resources.events.tabs.general")}>
        <TextField source="id" label="ID" />
        <TextField source="name" />
        <DateField source="dateStart" showTime />
        <DateField source="dateEnd" showTime />
        <FunctionField
          source="status"
          render={(r: Record | undefined) => <TextField
            emptyText={(translate(`resources.enums.eventStatus.${r?.status.toLowerCase()}`))}
          />}
        />
        <GeneralActions />
      </Tab>
      <Tab path="companies"
        label={translate('resources.events.tabs.companies')}>
        <RelationTab source={ResourceName.COMPANIES} mode='edit' />
      </Tab>
      <Tab path="objects"
        label={translate('resources.events.tabs.objects')}>
        <RelationTab source={ResourceName.OBJECTS} mode='edit' />
      </Tab>
      <Tab path="engines"
        label={translate('resources.events.tabs.engines')}>
        <RelationTab source={ResourceName.ENGINES} mode='edit' />
      </Tab>
      <Tab path="palms"
        label={translate('resources.events.tabs.palms')}>
        <RelationTab source={ResourceName.PALMS} mode='edit' />
      </Tab>
    </TabbedShowLayout>
  )
}

export const EventShow = (props: ShowProps) =>
  <Show {...props} actions={false} title={<ShowTitle />}>
    <EventTabs />
  </Show>