import { DataProvider, GetListResult, GetOneParams, GetOneResult, Identifier, UpdateParams, UpdateResult } from "react-admin"
import { PALMS_URL } from "../../api-urls"
import { CacheableDataProviderExtensionResult, DataProviderExtensionResult } from "../../common/data-provider"
import { get, getWithPagination, patch, put } from "../../common/fetch.utils"
import { filterParamsComposer, queryParamsComposer } from "../../common/get-by-conditions.utils"
import { TicketAPIGetListParams } from "../../common/ticket-api-get-list.params"
import { PalmEntrancesDto } from "../../dto/device/palm/palm-entrances.dto"
import { PalmEventsDto } from "../../dto/device/palm/palm-events.dto"
import { PalmDto } from "../../dto/device/palm/palm.dto"
import { PalmLockResult } from "../../enum/PalmLockResult"
import { PalmResetMobileDeviceIdResults } from "../../enum/PalmResetMobileDeviceIdResults"
import { PalmSetMobileDeviceIdResults } from "../../enum/PalmSetMobileDeviceIdResults"
import { PalmUnlockResult } from "../../enum/PalmUnlockResult"
import filterMapper from "./palm-filter.mapper"
import { mapSortPalmParam } from "./palm-sort.mapper"

const provider = {
  getList: async (
    resource: string,
    {
      filter: { ...filter },
      sort,
      pagination
    }: TicketAPIGetListParams
  ): Promise<GetListResult<PalmDto>> => {
    const filterParams = `o=>${filterParamsComposer('o', filter, filterMapper)}`
    const pathParams = queryParamsComposer(sort, pagination, mapSortPalmParam)
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const { data, range: { total } } = await getWithPagination<PalmDto[]>(`${PALMS_URL}/GetByConditions`, path)

    return Promise.resolve({
      data,
      total
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<PalmDto>> => {
    const data = await get<PalmDto>(PALMS_URL, `/${id}`)
    return Promise.resolve({
      data
    })
  },
  update: async (
    resource: string,
    { id, data, previousData }: UpdateParams<PalmDto>
  ): Promise<UpdateResult> => {
    const update = await patch<{ id: Identifier, [n: string]: any }, PalmDto>(PALMS_URL, {
      id,
      name: data.name !== previousData.name ? data.name : undefined,
      isWatched: data.isWatched !== previousData.isWatched ? data.isWatched : undefined,
      // lastRefresh: new Date(new Date(data.lastRefresh).getTime() - (new Date(data.lastRefresh).getTimezoneOffset() * 60000)).toJSON() as DateTime || previousData.lastRefresh,
      direction: data.direction !== previousData.direction ? data.direction : undefined,
      categoryId: data.categoryId !== previousData.categoryId ? data.categoryId : undefined,
      onlineInfixCeck: data.onlineInfixCeck !== previousData.onlineInfixCeck ? data.onlineInfixCeck : undefined,
      multiplicity: data.multiplicity !== previousData.multiplicity ? data.multiplicity : undefined,
    })
    return Promise.resolve({ data: update })
  },
  lock: async (
    resource: string,
    { palmId, lockName }: LockParams
  ): Promise<DataProviderExtensionResult<LockResponse>> => {
    const data = await put<LockParams, LockResponse>(`${PALMS_URL}/Lock`, {
      palmId,
      lockName
    })
    return Promise.resolve({ data })
  },
  unlock: async (
    resource: string,
    { palmId }: UnlockParams
  ): Promise<DataProviderExtensionResult<UnlockResponse>> => {
    const data = await put<UnlockParams, UnlockResponse>(`${PALMS_URL}/Unlock`, {
      palmId
    })
    return Promise.resolve({ data: { ...data, disabled: undefined } })
  },
  setMobileDeviceId: async (
    resource: string,
    { palmId, mobileDeviceId }: SetMobileDeviceIdParams
  ): Promise<DataProviderExtensionResult<SetMobileDeviceIdResponse>> => {
    const data = await put<SetMobileDeviceIdParams, SetMobileDeviceIdResponse>(`${PALMS_URL}/SetMobileDeviceId`, {
      palmId,
      mobileDeviceId
    })
    return Promise.resolve({ data })
  },
  resetMobileDeviceId: async (
    resource: string,
    { palmId }: ResetMobileDeviceIdParams
  ): Promise<DataProviderExtensionResult<ResetMobileDeviceIdResponse>> => {
    const data = await put<ResetMobileDeviceIdParams, ResetMobileDeviceIdResponse>(`${PALMS_URL}/ResetMobileDeviceId`, {
      palmId
    })
    return Promise.resolve({ data: { ...data, mobileDeviceId: undefined } })
  },
  attachEvents: async (
    resource: string,
    { palmId, eventsIds }: PalmEventsDto
  ): Promise<CacheableDataProviderExtensionResult<PalmEventsDto>> => {
    const data = await put<PalmEventsDto, PalmEventsDto>(`${PALMS_URL}/AttachEvents`, {
      palmId,
      eventsIds
    })
    return Promise.resolve({ data })
  },
  detachEvents: async (
    resource: string,
    { palmId, eventsIds }: PalmEventsDto
  ): Promise<CacheableDataProviderExtensionResult<PalmEventsDto>> => {
    const data = await put<PalmEventsDto, PalmEventsDto>(`${PALMS_URL}/DetachEvents`, {
      palmId,
      eventsIds
    })
    return Promise.resolve({ data })
  },
  attachEntrances: async (
    resource: string,
    { palmId, entrancesIds }: PalmEntrancesDto
  ): Promise<CacheableDataProviderExtensionResult<PalmEntrancesDto>> => {
    const data = await put<PalmEntrancesDto, PalmEntrancesDto>(`${PALMS_URL}/AttachEntrances`, {
      palmId,
      entrancesIds
    })
    return Promise.resolve({ data })
  },
  detachEntrances: async (
    resource: string,
    { palmId, entrancesIds }: PalmEntrancesDto
  ): Promise<CacheableDataProviderExtensionResult<PalmEntrancesDto>> => {
    const data = await put<PalmEntrancesDto, PalmEntrancesDto>(`${PALMS_URL}/DetachEntrances`, {
      palmId,
      entrancesIds
    })
    return Promise.resolve({ data })
  }
} as PalmDataProvider

interface PalmDataProvider extends DataProvider {
  lock: (
    resource: string,
    params: LockParams
  ) => Promise<DataProviderExtensionResult<LockResponse>>,

  unlock: (
    resource: string,
    params: UnlockParams
  ) => Promise<DataProviderExtensionResult<UnlockResponse>>,

  setMobileDeviceId: (
    resource: string,
    params: SetMobileDeviceIdParams
  ) => Promise<DataProviderExtensionResult<SetMobileDeviceIdResponse>>,

  resetMobileDeviceId: (
    resource: string,
    params: ResetMobileDeviceIdParams
  ) => Promise<DataProviderExtensionResult<ResetMobileDeviceIdResponse>>,

  attachEvents: (
    resource: string,
    params: PalmEventsDto
  ) => Promise<CacheableDataProviderExtensionResult<PalmEventsDto>>,

  detachEvents: (
    resource: string,
    params: PalmEventsDto
  ) => Promise<CacheableDataProviderExtensionResult<PalmEventsDto>>,

  attachEntrances: (
    resource: string,
    params: PalmEntrancesDto
  ) => Promise<CacheableDataProviderExtensionResult<PalmEntrancesDto>>,

  detachEntrances: (
    resource: string,
    params: PalmEntrancesDto
  ) => Promise<CacheableDataProviderExtensionResult<PalmEntrancesDto>>
}

export interface LockParams {
  palmId: Identifier,
  lockName: string
}

export interface LockResponse {
  palmId: Identifier,
  lockName: string,
  lockResult: PalmLockResult
}

export interface UnlockParams {
  palmId: Identifier
}

export interface UnlockResponse {
  palmId: Identifier,
  unlockResult: PalmUnlockResult
}

export interface SetMobileDeviceIdParams {
  palmId: Identifier,
  mobileDeviceId: string
}

export interface SetMobileDeviceIdResponse {
  palmId: Identifier,
  mobileDeviceId: string,
  setResult: PalmSetMobileDeviceIdResults
}

export interface ResetMobileDeviceIdParams {
  palmId: Identifier
}

export interface ResetMobileDeviceIdResponse {
  palmId: Identifier,
  resetResult: PalmResetMobileDeviceIdResults
}

export default provider