import React from "react"
import {
  ListProps, List, Pagination, TextField, FunctionField,
  Datagrid, useTranslate, Filter, TextInput
} from 'react-admin'
import { Authority } from "../../../core/auth/Authority"
import { VariantDto } from "../../../core/dto/variant.dto"
import { ResourceName } from "../../../core/ResourceName"
import FilterProps from '../../common/FilterProps'
import { useHasAuthority } from "../../hooks/useHasAuthority"

const VariantsFilters = (props: FilterProps) => (
  <Filter {...props}>
    <TextInput source="name" alwaysOn />
  </Filter>
)

export const VariantList = (props: ListProps) => {
  const translate = useTranslate()
  const hasAuthority = useHasAuthority()
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={<Pagination
        rowsPerPageOptions={[20, 50, 100]} />}
      sort={{ field: 'id', order: 'DESC' }}
      {...props}
      bulkActionButtons={false}
      filters={<VariantsFilters />}
      hasCreate={hasAuthority(Authority.CREATE_VARIANTS)}
    >
      <Datagrid rowClick={id =>
        `${ResourceName.VARIANTS}/${id}/show`}>
        <TextField source="id" />
        <TextField source="name" />
        <FunctionField<VariantDto>
          source="active"
          render={(record?: VariantDto) =>
            record?.active ?
              translate('const.yes') :
              translate('const.no')} />
      </Datagrid>
    </List>
  )
}