
const mapping: Partial<
  Record<
    string,
    (lambdaParameter: string, value: string | number | boolean, otherValues: any) => string | null
  >
> = {
  note: (lambdaParameter, value) => `${(lambdaParameter)}.Note.Contains("${value}")`,
  filterVerifierDeviceId: (lambdaParameter, value, otherValues) => value ? `${(lambdaParameter)}.VerifierDevices.Any(vd=>vd.Id==${otherValues?.verifierDeviceId})` : null,
  verifierDeviceId: () => null
}

export const getFilterMapper = (key: string) =>
  mapping[key] as (lambdaParameter: string, value: string | number | boolean, otherValues: any) => string
