import { DataProvider, GetListResult, GetOneParams, GetOneResult, UpdateParams, UpdateResult, CreateParams, CreateResult, GetManyParams, GetManyResult } from "react-admin"
import { VARIANTS_URL } from "../api-urls"
import { CacheableDataProviderExtensionResult } from "../common/data-provider"
import { get, getWithPagination, patch, post, put } from "../common/fetch.utils"
import { filterParamsComposer, queryParamsComposer } from "../common/get-by-conditions.utils"
import { TicketAPIGetListParams } from "../common/ticket-api-get-list.params"
import { VariantAccessesDto } from "../dto/variant-accesses.dto"
import { VariantDto } from "../dto/variant.dto"
import filterMapper from "./variants-filter.mapper"
import { mapSortVariantParam } from "./variants-sort.mapper"

const provider = {
  getList: async (
    resource: string,
    {
      filter: { ...filter },
      sort,
      pagination
    }: TicketAPIGetListParams
  ): Promise<GetListResult<VariantDto>> => {

    const filterParams = `v=>${filterParamsComposer('v', filter, filterMapper)}`
    const pathParams = queryParamsComposer(sort, pagination, mapSortVariantParam)
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const { data, range: { total } } = await getWithPagination<VariantDto[]>(`${VARIANTS_URL}/GetByConditions`, path)

    return Promise.resolve({
      data,
      total
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<VariantDto>> => {
    const data = await get<VariantDto>(VARIANTS_URL, `/${id}`)
    return Promise.resolve({
      data
    })
  },
  update: async (
    resource: string,
    { id, data, previousData }: UpdateParams<VariantDto>
  ): Promise<UpdateResult> => {
    const update = await patch<UpdateVariantRequest, VariantDto>(VARIANTS_URL, {
      id: id as number,
      name: data.name !== previousData.name ? data.name : undefined,
      active: data.active !== previousData.active ? data.active : undefined
    })
    return Promise.resolve({ data: update })
  },
  create: async (
    resource: string,
    { data }: CreateParams<VariantDto>
  ): Promise<CreateResult> => {
    const created = await post<CreateVariantRequest[], VariantDto[]>(VARIANTS_URL, [{
      name: data?.name,
      active: data?.active || false
    }])
    return Promise.resolve({ data: created[0] })
  },
  getMany: async (
    resource: string,
    { ids }: GetManyParams
  ): Promise<GetManyResult<VariantDto>> => {
    const data = await get<VariantDto[]>(VARIANTS_URL,
      `/GetByConditions/e=>new int[] {${ids.toString()}}.Contains(e.Id)`)
    return Promise.resolve({ data })
  },
  attachAccesses: async (
    resource: string,
    params: VariantAccessesDto
  ): Promise<CacheableDataProviderExtensionResult<VariantAccessesDto>> => {
    const variantAccesses = await put<VariantAccessesDto, VariantAccessesDto>
      (`${VARIANTS_URL}/AttachAccesses`, params)
    return {
      data: variantAccesses
    }
  },
  detachAccesses: async (
    resource: string,
    params: VariantAccessesDto
  ): Promise<CacheableDataProviderExtensionResult<VariantAccessesDto>> => {
    const variantAccesses = await put<VariantAccessesDto, VariantAccessesDto>
      (`${VARIANTS_URL}/DetachAccesses`, params)
    return {
      data: variantAccesses
    }
  }
} as VariantDataProvider

interface VariantDataProvider extends DataProvider {
  attachAccesses: (
    resource: string,
    params: VariantAccessesDto
  ) => Promise<CacheableDataProviderExtensionResult<VariantAccessesDto>>;

  detachAccesses: (
    resource: string,
    params: VariantAccessesDto
  ) => Promise<CacheableDataProviderExtensionResult<VariantAccessesDto>>;
};

type CreateVariantRequest = Pick<VariantDto, "name"> & Partial<Pick<VariantDto, "active">>

type UpdateVariantRequest = Pick<VariantDto, "id"> & Partial<Pick<VariantDto, "name" | "active">>

export default provider