import { useMediaQuery, useTheme } from '@material-ui/core'
import { Cancel } from '@material-ui/icons'
import React from 'react'
import {
  BooleanInput, DateTimeInput,
  Edit,
  EditProps,
  ReferenceInput,
  SaveButton,
  SelectInput,
  ShowButton,
  SimpleForm,
  TextField,
  TextInput,
  TitleProps,
  Toolbar,
  ToolbarProps,
  useTranslate
} from 'react-admin'
import { formatTime } from '../../../core/common/date-time'
import { AccessDto } from '../../../core/dto/access.dto'
import { AccessActivePeriodStartingOptions } from '../../../core/enum/AccessActivePeriodStartingOptions'
import { AccessBonuses } from '../../../core/enum/AccessBonuses'
import { DayOfWeek } from '../../../core/enum/DayOfWeek'
import { ResourceName } from '../../../core/ResourceName'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'

const validateAccessEdit = (values: AccessDto) => {
  const errors: { [n: string]: string } = {}
  if (!values.name) {
    errors.name = 'ra.validation.required'
  }
  if (!values.entranceId) {
    errors.entranceId = 'ra.validation.required'
  }
  if (Number.isNaN(Number(values.multiple)))
    errors.multiple = 'ra.validation.not_numeric'
  if (Number.isNaN(Number(values.usePriority)))
    errors.usePriority = 'ra.validation.not_numeric'
  if (Number.isNaN(Number(values.releaseDayOfMonth)))
    errors.releaseDayOfMonth = 'ra.validation.not_numeric'
  return errors
}

interface AccessTitleProps extends TitleProps {
  record?: AccessDto
}

const EditTitle = (data: AccessTitleProps) => {
  const {
    record,
  } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.accesses.titles.edit')}: {record?.id}. {record?.name}{' '}
    </span>
  )
}

const EditToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ShowButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

export const AccessEdit = (props: EditProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down("sm"))
  return (
    <Edit
      {...props}
      hasShow={false}
      title={<EditTitle />}>
      <SimpleForm
        redirect="show"
        toolbar={<EditToolbar />}
        validate={validateAccessEdit}>
        <TextField
          source="id"
          label="ID" />
        <TextInput
          source="name" />
        <SelectInput
          source="bonus"
          choices={[
            { id: null, name: "N/A" },
            ...Object.entries(AccessBonuses)
              .map(([value]) => ({ id: value, name: `resources.enums.bonus.${value}` }))
          ]} />
        <ReferenceInput
          source="entranceId"
          reference={ResourceName.ENTRANCES}
          sort={{ field: 'note', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="note"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
        <BooleanInput
          source="allowReenter" />
        <BooleanInput
          source="multipleEntry" />
        <TextInput
          source="multiple" />
        <TextInput
          source="usePriority" />
        <BooleanInput
          source="isTicketUnlockedWithNextDay" />
        <DateTimeInput
          source="releaseTime"
          type="time"
          format={formatTime}
          parse={(value: string) => value}
        />
        <SelectInput
          source="releaseDayOfWeek"
          choices={[
            { id: null, name: "N/A" },
            ...Object.entries(DayOfWeek)
              .map(([value]) =>
                ({ id: value, name: `resources.enums.dayOfWeek.${value}` }))
          ]} />
        <TextInput
          source="releaseDayOfMonth" />
        <TextInput
          source="delay" />
        <TextInput
          source="activePeriod" />
        <SelectInput
          source="activePeriodStartingFrom"
          choices={[
            { id: null, name: "N/A" },
            ...Object.entries(AccessActivePeriodStartingOptions)
              .map(([value]) =>
                ({ id: value, name: `resources.enums.activePeriodStartingFrom.${value}` }))
          ]} />
      </SimpleForm>
    </Edit>
  )
}