import React from "react"
import { ListProps, List, Pagination, FunctionField, TextField, Datagrid, ReferenceField, DateField } from "react-admin"
import { useMediaQuery, useTheme } from "@material-ui/core"
import { MobilePostPagination } from '../common/Pagination'
import { ResourceName } from "../../core/ResourceName"
import { EventDto } from "../../core/dto/event/event.dto"

export const BookingList = (props: ListProps) => {
  const theme = useTheme()
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={
        useMediaQuery(theme.breakpoints.up('md')) ? (
          <Pagination rowsPerPageOptions={[20, 50, 100]} />
        ) : (
          <MobilePostPagination />
        )
      }
      sort={{ field: 'id', order: 'DESC' }}
      {...props}
      bulkActionButtons={false}
    >
      <Datagrid rowClick={(id) => `/${ResourceName.TICKET_DETAILS}/${id}/show`}>
        <ReferenceField link={false}
          reference={ResourceName.EVENTS}
          source="eventId">
          <FunctionField<EventDto>
            render={() => (
              <div> <TextField source="id" />. <TextField source="name" /> <DateField source="dateStart" /> </div>
            )} />
        </ReferenceField>
        <TextField source="id" />
        <ReferenceField link={false}
          reference={ResourceName.VARIANTS}
          source="variantId">
          <TextField source="name" />
        </ReferenceField>
      </Datagrid>
    </List>
  )
}