import { useMediaQuery, useTheme } from "@material-ui/core"
import React from "react"
import { List, ListProps, Datagrid, TextField, Pagination, FunctionField, useTranslate, DateField, TextInput, SelectInput, Filter } from 'react-admin'
import { Authority } from "../../../core/auth/Authority"
import { AccessDto } from "../../../core/dto/access.dto"
import { ResourceName } from "../../../core/ResourceName"
import FilterProps from '../../common/FilterProps'
import QuickFilter from "../../common/QuickFilter"
import { useHasAuthority } from "../../hooks/useHasAuthority"

const AccessFilters = (props: FilterProps) => {
  const translate = useTranslate()
  return (
    <Filter {...props}>
      <TextInput source="name" alwaysOn />
      <TextInput source="id" />
      <SelectInput source="bonus"
        choices={[
          {
            id: 'N',
            name: translate('resources.enums.bonus.N')
          },
          {
            id: 'Y',
            name: translate('resources.enums.bonus.Y')
          },
          {
            id: 'Z',
            name: translate('resources.enums.bonus.Z')
          },
          {
            id: 'D',
            name: translate('resources.enums.bonus.D')
          },
        ]} />
      <TextInput source="entranceId" />
      <QuickFilter source="allowReenter" />
      <QuickFilter source="multipleEntry" />
      <QuickFilter source="isTicketUnlockedWithNextDay" />
    </Filter>
  )
}

export const AccessList = (props: ListProps) => {
  const translate = useTranslate()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={<Pagination rowsPerPageOptions={[20, 50, 100]} />}
      sort={{ field: 'id', order: 'DESC' }}
      {...props}
      bulkActionButtons={false}
      filters={
        <AccessFilters />
      }
      hasCreate={hasAuthority(Authority.CREATE_ACCESSES)}
    >
      <Datagrid rowClick={id => `${ResourceName.ACCESSES}/${id}/show`}>
        <TextField source="name" />
        <FunctionField<AccessDto> source="bonus" render={(record?: AccessDto) => translate(`resources.enums.bonus.${record?.bonus}`)} />
        <TextField source="entranceId" />
        {!smallScreen && <DateField source="modifiedAt" />}
        {!smallScreen && <DateField source="createdAt" />}
      </Datagrid>
    </List>
  )
}