import { DataProvider, GetListResult } from "react-admin"
import { ADMINISTRATOR_URL } from "../api-urls"
import { getWithPagination } from "../common/fetch.utils"
import { filterParamsComposer, queryParamsComposer } from "../common/get-by-conditions.utils"
import { TicketAPIGetListParams } from "../common/ticket-api-get-list.params"
import { CompanyDto } from "../dto/company.dto"
import filterMapper from "./company-filter.mapper"
import { mapSortCompanyParam } from "./company-sort.mapper"

const provider = {
  getList: async (
    resource: string,
    {
      filter: { ...filter },
      sort,
      pagination
    }: TicketAPIGetListParams
  ): Promise<GetListResult<CompanyDto>> => {

    const filterParams = `o=>${filterParamsComposer('o', filter, filterMapper)}`
    const pathParams = queryParamsComposer(sort, pagination, mapSortCompanyParam)
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const { data, range: { total } } = await getWithPagination<CompanyDto[]>(`${ADMINISTRATOR_URL}/GetCompaniesByConditions`, path)

    return Promise.resolve({
      data,
      total
    })
  }
} as DataProvider

export default provider